$breakpoint-hd-laptop: 1366px;
$breakpoint-tablet: 768px;
$breakpoint-laptop: 992px;
$breakpoint-phone: 576px;

#shipdeo-navbar {
    background: #ffff !important;
    // background: transparent !important;
    transition: ease-in 0.4s;

    #btn-login-register {
        text-align: right;

        a {
            width: 100px;
            border-radius: 12px;
        }
    }

    .navbar-account-btn{
        // background-color: #0AC2FF;
    }

    .btn-info {
        background-color: #0AC2FF;
        border: 1px solid #0AC2FF;
        border-radius: 12px;
        color: white;
    }

    .btn-outline-info {
        color: #0AC2FF;
        background-color: white;
        border: 1px solid #0AC2FF;
    }

    .btn-outline-info:hover {
        color: white;
        background-color: #0AC2FF;
        border: 1px solid #0AC2FF;
    }

    .navbar-btn-separator {
        height: 10px;
        width: 100%;
    }

    .navbar-brand {
        @media (max-width: $breakpoint-phone) {
            margin-top: 10px;
            margin-left: -25px;
        }
    }

    .nav-item {
        .dropdown-menu {
            left: 22px;
            top: 50px;
            background: #ffffff !important;
            box-shadow: 0px 4px 15px rgba(105, 105, 105, 0.15);
            border-radius: 0px 20px 20px 20px;
            border: unset;
            width: 245px;

            @media (max-width: $breakpoint-laptop) {
                // width: 50%;
            }
    
            @media (max-width: $breakpoint-tablet) {
                // width: 50%;
                // left: 25px;
            }
    
            @media (max-width: $breakpoint-phone) {
                width: 100px;
                text-align: center;
                // margin: auto 0;
                // margin-right: 300px;
            }

            a {
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 29px;
                /* identical to box height, or 211% */

                color: #000000;

                &:hover {
                    background-color: unset;
                }
            }
        }
    }

    li {
        padding: 10px 20px 10px 20px;
        a {
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 21px;

            color: #3d3d3d;
        }
    }
    .navbar-toggler-icon {
        background: gray;
        border-radius: 1px;
        border-width: 1em;
        border-color: #000000;
    }
}

.shipdeo-navbar-sticky {
    position: fixed !important;
    top: 0;
    width: 100%;
    z-index: 1000;
    box-shadow: 1px 1px 15px #D0D0D0;
}

.shipdeo-navbar-bg-sticky {
    background-color: white !important;
    transition: ease-in 0.4s;
}

